(() => {
  const menuBtnRef = document.querySelector('[data-menu-button]');
  const mobileMenuRef = document.querySelector('[data-menu]');
  menuBtnRef.addEventListener('click', () => {
    const expanded = menuBtnRef.getAttribute('aria-expanded') === 'true' || false;
    menuBtnRef.classList.toggle('is-open');
    if (menuBtnRef.classList.contains('is-open')) {
      document.body.style.overflow = 'hidden'
    } else {
       document.body.style.overflow = ''
    }
    menuBtnRef.setAttribute('aria-expanded', !expanded);
    mobileMenuRef.classList.toggle('is-open');
  });
})();
(() => {
  const menuBtnRef = document.querySelector('[data-menu-button]');
  const mobileMenuRef = document.querySelector('[data-menu]');
  const menuLinkRef = document.querySelectorAll('.menu__navigation-link');
  console.log(menuLinkRef);
  menuBtnRef.addEventListener('click', () => {
    const expanded = menuBtnRef.getAttribute('aria-expanded') === 'true' || false;
    menuBtnRef.classList.toggle('is-overflow');
    menuBtnRef.setAttribute('aria-expanded', !expanded);
    mobileMenuRef.classList.toggle('is-overflow');
  });
  menuLinkRef.forEach(linkRef => {
    linkRef.addEventListener('click', () => {
      const expanded = menuBtnRef.getAttribute('aria-expanded') === 'true' || false;
      menuBtnRef.classList.toggle('is-open');
    if (menuBtnRef.classList.contains('is-open')) {
      document.body.style.overflow = 'hidden'
    } else {
       document.body.style.overflow = ''
    }
      menuBtnRef.setAttribute('aria-expanded', !expanded);
      mobileMenuRef.classList.toggle('is-open');
    });
  });
})();